<template>
  <div class="propertyOrderUnpay">
    <div class="propertyOrderUnpayContent" :class="{propertyOrderUnpayContentThemeL:bindThemeL}" v-if="CountDown==true">
      <div class="pay" :class="{payThemeL:bindThemeL}">
        <div class="payNew" :class="{payNewThemeL:bindThemeL}">
          <div class="payText" :class="{payTextThemeL:bindThemeL}">您有新的待支付订单！</div>
          <div class="payText" :class="{payTimeThemeL:bindThemeL}">剩余时间：{{payCountDown}}</div>
        </div>
        <div class="goPay" :class="{goPayThemeL:bindThemeL}" @click="gotoPaid()">去支付</div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getAllOrderListService,
  orderCancelService,
  preOrderCancelService
} from "../../../services/propertyBillService";
export default {
  name: "propertyOrderUnpay",
  data() {
    return {
      orderList: [],
      payCountDown: "",
      CountDown: false,
      timePromise: null,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  mounted() {
    this.getAllOrderList();
  },
  methods: {
    refreshOrder() {
      this.orderList = [];
      this.clearPayTimer();
      this.getAllOrderList();
    },
    clearPayTimer(){
      this.payCountDown = "";
      this.CountDown = false;
      console.log('清除timePromise = '+this.timePromise);
      clearInterval(this.timePromise);
    },
    getAllOrderList() {
      var reqData = {
        userCode: this.$userInfo.OWNER_CODE,
        orderStatus: 0
      };
      getAllOrderListService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode == 200) {
            this.orderList = result.data.billList;
            if (this.orderList.length > 0) {
              clearInterval(this.timePromise); //终止倒计时
              this.setCountDown(
                this.orderList[0].createdTime,
                this.orderList[0].serverTime
              );
            }
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          console.log(JSON.stringify(error));
          this.$toast("网络异常，请稍后重试！");
        });
    },
    setCountDown(createTime, serverTime) {
      var createTimes = ""; //订单创建时间
      var serverTimes = ""; //订单创建时间
      var curTime; //当前时间
      var totalSecond = 0; //设置计时总时间（分钟）
      if (createTime != null) {
        //为了支持safari浏览器
        createTimes = new Date(createTime.replace(/\-/g, "/")).getTime();
        serverTimes = new Date(serverTime.replace(/\-/g, "/")).getTime();
        totalSecond = Math.round(
          (createTimes + 15 * 60 * 1000 - serverTimes) / 1000
        );
      } else {
        totalSecond = 15 * 60;
      }

      //倒计时
      this.timePromise = setInterval(() => {
        this.CountDown = true;
        if (totalSecond >= 0) {
          var t1 = Math.floor(totalSecond / 60);
          var m = t1 < 10 ? "0" + t1 : t1;
          var t2 = totalSecond - t1 * 60;
          var s = t2 < 10 ? "0" + t2 : t2;
          totalSecond = totalSecond - 1;
          this.payCountDown = m + "分钟" + s + "秒";
        } else {
          this.CountDown = false;
          console.log('此时触发的timePromise = '+this.timePromise);
          clearInterval(this.timePromise); //终止倒计时
          //this.orderCancel(); //暂时注释因接口未上传服务器
        }
      }, 1000);
      
      console.log('设置timePromise = '+this.timePromise);
    },
    gotoPaid() {
      let order = this.orderList[0];
      if (order.billType == "预缴费") {
        this.$router.push({
          name: "propertyPreOrderPayDetail",
          params: {
            billNo: order.billNo
          }
        });
      } else {
        this.$router.push({
          name: "propertyOrderPayDetail",
          params: {
            billCode: order.billNo,
            areaCode: order.areaCode
          }
        });
      }
    },
    orderCancel() {
      let order = this.orderList[0];
      if (order.billType == "预缴费") {
        var reqData = {
          billNo: order.billNo,
          cancelWay: "01"
        };
        preOrderCancelService(reqData)
          .then(result => {
            //请求成功
            this.refreshOrder();
          })
          .catch(error => {
            //加载失败后，关闭加载等待的文字
          });
      } else {
        var reqData = {
          bill_code: order.billNo
        };
        orderCancelService({ data: JSON.stringify(reqData) })
          .then(result => {
            //请求成功
            this.refreshOrder();
          })
          .catch(error => {
            //加载失败后，关闭加载等待的文字
          });
      }
    }
  }
};
</script>

<style scoped>
.propertyOrderUnpay .propertyOrderUnpayContent {
  height: 75px;
  width: 100%;
  background-image: linear-gradient(-270deg, #fe9457 0%, #ffb15e 100%);
}
.propertyOrderUnpay .propertyOrderUnpayContentThemeL {
  height: 100px !important;
}
.propertyOrderUnpay .propertyOrderUnpayContentFont {
  font-size: 14px;
  color: #ffffff;
}
.propertyOrderUnpay .pay {
  display: flex;
  background-image: linear-gradient(-270deg, #ffb15e 0%, #fe9457 100%);
  height: 74px;
  /* margin-top: 22px; */
}
.propertyOrderUnpay .payThemeL {
  height: 100px !important;
}
.propertyOrderUnpay .payText {
  height: 20px;
  font-family: PingFangSC-Medium;
  font-size: 14px;
  color: #ffffff;
  letter-spacing: 0.06px;
}

.propertyOrderUnpay .payTextThemeL {
  font-size: 20px !important;
  height: 30px !important;
}
.propertyOrderUnpay .payTimeThemeL {
  font-size: 15px !important;
  height: 30px !important;
  line-height: 30px !important;
}

.propertyOrderUnpay .payNew {
  margin-top: 15px;
  margin-left: 20px;
  flex-grow: 1;
}
.propertyOrderUnpay .payNewThemeL {
  margin-top: 20px !important;
}
.propertyOrderUnpay .goPay {
  margin-right: 20px;
  height: 29px;
  line-height: 2;
  width: 20%;
  background: #fff;
  margin-top: 22px;
  border-radius: 20px;
  text-align: center;
  font-size: 14px;
}
.propertyOrderUnpay .goPayThemeL {
  font-size: 15px !important;
  margin-top: 35px;
}
</style>
