<template>
  <div class="propertyBillAmount">
    <div class="order_pay">
      <div class="order_word">
        <img
          v-if="isShowCheck"
          width="25"
          @click="checkAllFn()"
          :src="isAllCheck?billCheckedImg:billCheckingImg"
          :style="{marginTop:imgMarginTop}"
        />
      </div>
      <div class="orderNum" :class="{orderNumThemeL:bindThemeL}" v-if="isShowCheck">全选</div>
      <div class="order_amount" :class="{order_amount_themeL:bindThemeL}">
        总计:
        <span style="font-size: 16px;font-weight: bolder;">￥{{amount| keepTwoNum}}</span>
      </div>
      <div class="soonPay" :class="{soonPayThemeL:bindThemeL}" @click="createOrderPay()">缴费</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "propertyBillAmount",
  props: {
    amount: Number,
    isAllCheck: { type: Boolean, default: false },
    isShowCheck: { type: Boolean, default: false },
    propertyBillList: Array
  },
  data() {
    return {
      billCheckingImg: require("../../../assets/images/home/property/payment/list_btn_check_no.png"),
      billCheckedImg: require("../../../assets/images/home/property/payment/list_btn_check.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      imgMarginTop:this.$curTheme==0?"14px":"12px"
    };
  },
  methods: {
    checkAllFn() {
      this.$emit("checkAllFn");
    },
    createOrderPay() {
      this.$emit("createOrderPay");
    }
  }
};
</script>

<style scoped>
.propertyBillAmount .order_pay {
  /*  overflow: hidden;*/
  position: fixed;
  width: 100%;
  left: 0;
  height: 50px;
  line-height: 50px;
  background-color: #fff;
  bottom: 0;
  box-shadow: 0 -2px 7px 0 rgba(226, 226, 226, 0.5);
}
.propertyBillAmount .order_word {
  float: left;
  margin-left: 5%;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #3a3a3a;
  letter-spacing: 0.06px;
  height: 50px;
  line-height: 50px;
}
.propertyBillAmount .order_amount {
  float: left;
  font-family: PingFangSC-Regular;
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 12px;
  margin-left: 10px;
}
.propertyBillAmount .order_amount_themeL {
  font-size: 16px;
  margin-left: 35px;
}
.propertyBillAmount .order_amount_themeL span {
  font-size: 20px !important;
}
.propertyBillAmount .orderNum {
  float: left;
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #9a9a9a;
  line-height: 50px;
  height: 50px;
}
.propertyBillAmount .orderNumThemeL {
  font-size: 15px;
}
.propertyBillAmount .soonPay {
  background: linear-gradient(to right, #fd818d, #fb5364);
  /* box-shadow: 0 0 8px 0 rgba(0, 209, 203, 0.29); */
  /* border-radius: 6px; */
  width: 30%;
  height: 50px;
  float: right;
  line-height: 50px;
  text-align: center;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #ffffff;
  letter-spacing: 0.06px;
  /* margin-right: 20px; */
  /*padding: 0 7px;*/
  right: 0;
  position: absolute;
}
.propertyBillAmount .soonPayThemeL {
  font-size: 18px;
}
</style>
