<template>
  <div class="propertyBillNone">
    <div class="propertyBillNoneContent" :class="{themeL:bindThemeL}">
      <van-image width="150" :src="defalutImg" />
      <div class="propertyBillNoneContentFont">暂无待缴账单</div>

      <div class="goPrePay" @click="gotoAdvancePayment()" v-if="isShowPrePay">我要预缴</div>
      <div class="goCommonPhone" @click="gotoCommonPhone()" v-if="isShowPrePay">有疑问？点此联系物业</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "propertyBillNone",
  props: {
    isShowPrePay: Boolean
  },
  data() {
    return {
      defalutImg: require("../../../assets/images/home/property/payment/img_default@2x.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  methods: {
    gotoAdvancePayment() {
      this.$emit("gotoAdvancePayment");
    },
    gotoCommonPhone(){
      this.$router.push({ name: "commonPhone" }); //常用电话
    }
  }
};
</script>

<style scoped>
