}
.propertyBillNone .propertyBillNoneContentFont {
  font-size: 14px;
  color: #9a9a9a;
}
.propertyBillNone .themeL .propertyBillNoneContentFont {
  font-size: 15px;
}
.propertyBillNone .goPrePay {
  width: 100px;
  margin: 0 auto;
  margin-top: 20px;
  text-align: center;
  border-radius: 14.5px;
  border: 1px solid #fb5768;
  height: 30px;
  width: 92px;
  line-height: 30px;
  color: #fb5768;
  font-size: 14px;
}
.propertyBillNone .themeL .goPrePay {
