<template>
  <div class="propertyBillList">
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
          <div class="rightBtn" :class="{rightBtnThemeL:bindThemeL}" @click="onClickRight">{{rightName}}</div>
      </template>
    </navigation-bar>

    <div class="propertyBillListContent">
      <property-order-unpay ref="unpay"></property-order-unpay>
      <div v-if="propertyBillList.length>0" class="billAlert" :class="{billAlertThemeL:bindThemeL}">您有新的待缴账单，请及时处理！</div>
      <property-bill-none
        v-if="isShowDefault"
        :isShowPrePay="isShowPrePay"
        @gotoAdvancePayment="gotoAdvancePayment()"
      ></property-bill-none>
      <div v-if="propertyBillList.length>0">
        <!-- 房间列表 -->
        <div class="order_itme_list" v-for="(data,index) in propertyBillList" :key="index">
          <div class="orderHeader">
            <div style="width: 10%;text-align: center; margin-left: 8px;">
              <img
                width="25"
                @click="checkAllFn(data)"
                :src="data.isCheckAll?billCheckedImg:billCheckingImg"
              />
            </div>
            <div style="width:90%;">
              <div class="user_title" :class="{user_title_themeL:bindThemeL}">{{data.room_name}}</div>
              <div class="user_name" :class="{user_name_themeL:bindThemeL}">房主：{{data.owner_name}}</div>
            </div>
          </div>
          <!-- 月份账单 -->
          <div style="-webkit-overflow-scrolling: touch;overflow-y:false;">
            <div v-for="(x,MonthIndex) in data.monthList" :key="MonthIndex">
              <div
                style="display: flex;height: 56px;line-height: 56px;border: 1px solid #EFEFEF;border-top:none;background-color: white;"
                v-bind:class="MonthIndex==data.monthList.length-1?'monthUnderRadius': ''"
              >
                <div style="width: 50%;display: flex;" @click="checkFn(x,data)">
                  <div
                    style="width: 20%;text-align: center;height: 53px;line-height: 53px;margin-left: 8px;"
                  >
                    <img
                      style="margin-top:14px;"
                      width="25"
                      :src="x.isCheck?billCheckedImg:billCheckingImg"
                    />
                  </div>
                  <div style="width: 80%;font-size:14px;" :style="{fontSize:monthFontSize}">{{x.month_name}}</div>
                </div>
                <div style="width: 50%;position: relative;" @click="showTime(index,MonthIndex)">
                  <div
                    style=" width: 80%;text-align: right;font-size:14px;"
                    :style="{fontSize:amountFontSize}"
                  >￥{{x.month_amount| keepTwoNum}}</div>
                  <div style=" position: absolute;right: 8%;top: -1px;">
                    <img width="14" style="vertical-align: middle;" :src="x.isShow?arrowDownImg:arrowRightImg" />
                  </div>
                </div>
              </div>
              <div v-if="x.isShow">
                <div v-for="(item,detailIndex) in x.billDetailList" :key="detailIndex">
                  <div class="freeShow">
                    <div class="freeText" :class="{freeTextThemeL:bindThemeL}">{{item.payment_type}}</div>
                    <div class="freeText" :class="{freeTextThemeL:bindThemeL}">￥{{item.payment_amount| keepTwoNum}}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      <div class="goCommonPhone" :class="{goCommonPhoneThemeL:bindThemeL}" @click="gotoCommonPhone()" v-if="isShowPrePay">有疑问？点此联系物业</div>
      </div>
      

    </div>

      <div class="bottomPanel" v-if="propertyBillList.length <= 0">
        <div class="addRoom" :class="{addRoomThemeL:bindThemeL}" @click="addNewRoom">+ 新增缴费</div>
      </div>
    <property-bill-amount
      v-if="propertyBillList.length > 0"
      :amount="amount"
      :isAllCheck="isAllCheck"
      :propertyBillList="propertyBillList"
      @checkAllFn="allCheckFn()"
      @createOrderPay="createOrderPay()"
      :isShowCheck="true"
    ></property-bill-amount>
  </div>
</template>

<script>
import {
  propertyfeeService,
  createOrderPayService,
  queryBillDetailService
} from "../../../services/propertyBillService";
import propertyBillNone from "./propertyBillNone";
import propertyOrderUnpay from "./propertyOrderUnpay";
import propertyBillAmount from "./propertyBillAmount";
import NavigationBar from '@/common/currency/NavigationBar.vue';

export default {
  name: "propertyBillList",
  mounted() {
  },
  beforeRouteLeave(to, from, next){
    //清理倒计时
    this.$refs.unpay.clearPayTimer();
    next();
  },
  activated(){
    this.amount = 0;
    this.isAllCheck = false;
    this.saveList = [];
    this.propertyBillList = [];
    this.isShowPrePay = true;
    this.isShowDefault = false;
    this.areaCode = this.$route.params.areaCode
      ? this.$route.params.areaCode
      : this.areaCode;
    this.$refs.unpay.refreshOrder();
    this.propertyfee();
  },
  data() {
    return {
      amount: 0, //待缴金额
      isAllCheck: false,
      areaCode: this.$userInfo.AREA_CODE,
      saveList: [], //提交缴费前重组数组匹配接口格式
      propertyBillList: [],
      isShowPrePay:true,
      isShowDefault:false,
      headerItemWidth:{
          width:'110px'
      },
      title:'待缴账单',
      rightName:'缴费记录',
      billCheckingImg: require("../../../assets/images/home/property/payment/list_btn_check_no.png"),
      billCheckedImg: require("../../../assets/images/home/property/payment/list_btn_check.png"),
      arrowRightImg: require("../../../assets/images/home/property/payment/icon_arrow_right_black.png"),
      arrowDownImg: require("../../../assets/images/home/property/payment/icon_arrow_down_black.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      monthFontSize: this.$curTheme==0?"14px":"18px",
      amountFontSize: this.$curTheme==0?"14px":"17px"
    };
  },
  components: {
    NavigationBar,
    "property-bill-none": propertyBillNone,
    "property-order-unpay": propertyOrderUnpay,
    "property-bill-amount": propertyBillAmount
  },

  methods: {
    propertyfee() {
      var reqData = {
        owner_code: this.$userInfo.OWNER_CODE,
        area_code: this.areaCode
      };
      propertyfeeService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result.result) {
            for (let index in result.data.roomList) {
              result.data.roomList[index].isCheckAll = false;
              for (let monthIndex in result.data.roomList[index].monthList) {
                result.data.roomList[index].monthList[
                  monthIndex
                ].isCheck = false;
                result.data.roomList[index].monthList[
                  monthIndex
                ].isShow = false;
              }
            }
            console.log(JSON.stringify(result));
            this.propertyBillList = result.data.roomList;
            if(this.propertyBillList.length <=0)
              this.isShowDefault = true;
          } else {
            this.isShowDefault = true;
            if(result.msg != '暂无账单~'){
              this.isShowPrePay = false;
              this.$toast(result.msg);
            }
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    queryBillDetail(parentIndex, index) {
      var reqData = {
        owner_code: this.$userInfo.OWNER_CODE,
        area_code: this.$userInfo.AREA_CODE,
        room_code: this.propertyBillList[parentIndex].room_code,
        month_code: this.propertyBillList[parentIndex].monthList[index]
          .month_code
      };
      queryBillDetailService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result.result) {
            if (result.data.billDetailList) {
              this.propertyBillList[parentIndex].monthList[
                index
              ].billDetailList = result.data.billDetailList;

              this.propertyBillList[parentIndex].monthList[index].isShow = !this
                .propertyBillList[parentIndex].monthList[index].isShow;
            } else {
              this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 获取某月账单物业费明细 */
    showTime(parentIndex, index) {
      //判断月份详情是否存在，不存在进行网络请求
      if (
        this.propertyBillList[parentIndex].monthList[index].billDetailList ==
          null ||
        this.propertyBillList[parentIndex].monthList[index].billDetailList ==
          "undefined"
      ) {
        this.queryBillDetail(parentIndex, index);
      } else {
        this.propertyBillList[parentIndex].monthList[index].isShow = !this
          .propertyBillList[parentIndex].monthList[index].isShow;
      }
    },
    /* 账单全选 */
    checkAllFn(area) {
      area.isCheckAll = !area.isCheckAll;
      for (var j = 0; j < area.monthList.length; j++) {
        area.monthList[j].isCheck = area.isCheckAll;
        if (area.monthList[j].isCheck == false) {
          area.isCheckAll = false;
        }
      }
      //底部全选状态控制
      for (var j = 0; j < this.propertyBillList.length; j++) {
        if (this.propertyBillList[j].isCheckAll == true) {
          if (this.propertyBillList.length - 1 == j) {
            this.isAllCheck = true;
          }
        } else {
          this.isAllCheck = false;
          break;
        }
      }

      //选中的后金额
      this.moneyAll();
    },
    checkFn(bill, area) {
      //按周期规则选中
      this.paymentCycle(bill, area);
      //单选全选后，全选选中状态
      for (var i = 0; i < area.monthList.length; i++) {
        if (area.monthList[i].isCheck == true) {
          if (i == area.monthList.length - 1) {
            area.isCheckAll = true;
          }
        } else {
          area.isCheckAll = false;
          this.isAllCheck = false;
          break;
        }
      }

      var num = 0;
      //单选全选后，总全选选中状态
      for (var j = 0; j < this.propertyBillList.length; j++) {
        if (this.propertyBillList[j].isCheckAll == true) {
          num++;
          if (num == this.propertyBillList.length) {
            this.isAllCheck = true;
          }
        } else {
          this.isAllCheck = false;
        }
      }

      //选中的后金额
      this.moneyAll();
    },
    allCheckFn() {
      this.isAllCheck = !this.isAllCheck;
      for (let index in this.propertyBillList) {
        this.propertyBillList[index].isCheckAll = this.isAllCheck;
        for (let monthIndex in this.propertyBillList[index].monthList) {
          this.propertyBillList[index].monthList[
            monthIndex
          ].isCheck = this.isAllCheck;
        }
      }
      //选中的后金额
      this.moneyAll();
    },
    //按周期选中
    paymentCycle(res, data) {
      var isSelected = res.isCheck;
      //截取选中月份
      var resSub = parseInt(
        res.month_code.substring(res.month_code.length - 2)
      );
      //截取选中年份
      var resSubYear = parseInt(res.month_code.substring(0, 4));
      for (var i = 0; i < data.monthList.length; i++) {
        //返回数据中的月份截取
        var resSubNew = parseInt(
          data.monthList[i].month_code.substring(res.month_code.length - 2)
        );
        //截取年份
        var dataSubYear = parseInt(
          data.monthList[i].month_code.substring(0, 4)
        );
        if (!isSelected) {
          if (resSubYear === dataSubYear) {
            if (
              resSub >= resSubNew ||
              parseInt((resSub - 1) / data.paymentCycle) ===
                parseInt((resSubNew - 1) / data.paymentCycle)
            ) {
              data.monthList[i].isCheck = !isSelected;
            }
          } else if (resSubYear > dataSubYear) {
            data.monthList[i].isCheck = !isSelected;
          }
        } else {
          if (resSubYear === dataSubYear) {
            if (
              resSub <= resSubNew ||
              parseInt((resSub - 1) / data.paymentCycle) ===
                parseInt((resSubNew - 1) / data.paymentCycle)
            ) {
              data.monthList[i].isCheck = !isSelected;
            }
          } else if (resSubYear < dataSubYear) {
            data.monthList[i].isCheck = !isSelected;
          }
        }
      }
    },
    //总支付金额
    moneyAll() {
      this.amount = 0;
      let roomList = this.propertyBillList;
      for (var i = 0; i < roomList.length; i++) {
        var monthList = roomList[i].monthList;
        for (var j = 0; j < monthList.length; j++) {
          var monthInfo = monthList[j];
          if (monthInfo.isCheck) {
            this.amount += monthInfo.month_amount;
          }
        }
        this.amount = Math.round(this.amount * 100) / 100;
      }
      console.log(this.amount);
    },
    //组装数组，匹配接口传入格式
    saveListFn() {
      this.saveList = [];
      var monthList = [];
      var room = {};
      for (var i = 0; i < this.propertyBillList.length; i++) {
        room = {};
        if (this.propertyBillList[i].isCheckAll == true) {
          room = this.propertyBillList[i];
        } else {
          monthList = [];
          var k = 0;
          for (var j = 0; j < this.propertyBillList[i].monthList.length; j++) {
            if (this.propertyBillList[i].monthList[j].isCheck == true) {
              monthList.push(this.propertyBillList[i].monthList[j]);
              k++;
            }
          }
          if (k > 0) {
            room.owner_name = "6797_yz";
            room.paymentCycle = this.propertyBillList[i].paymentCycle;
            room.room_code = this.propertyBillList[i].room_code;
            room.room_name = this.propertyBillList[i].room_name;
            room.monthList = monthList;
          }
        }
        this.saveList.push(room);
      }
    },
    //生成缴费订单
    createOrderPay() {
      //组装数组，匹配接口传入格式
      this.saveListFn();
      if (this.saveList.length <= 0) return;
      //删除无用数据
      for (let i = this.saveList.length - 1; i >= 0; i--) {
        if (this.saveList[i].monthList == undefined) {
          this.saveList.splice(i, 1);
        }
      }

      var reqData = {
        area_code: this.areaCode,
        owner_code: this.$userInfo.OWNER_CODE,
        bill_amount: this.amount,
        billDetail: {
          roomList: this.saveList
        }
      };
      createOrderPayService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result.result) {
            //返回订单号和银联参数；需要加非空判断
            if (result.bill_code) {
              console.log("生成订单" + JSON.stringify(result));
              this.$router.push({
                name: "propertyOrderPay",
                params: {
                  areaCode: result.data.area_code,
                  billCode: result.data.billNo
                }
              });
            } else {
              this.$toast("没有查询到数据！");
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //跳转账单历史
    gotoRoomManager() {
      this.$router.push({
        name: "paymentRoomManager"
      });
    },
    //跳转预缴费
    gotoAdvancePayment() {
      this.$router.push({
        name: "propertyPreBill",
        params: { areaCode: this.areaCode }
      });
    },
    onClickLeft() {
      this.$router.back();
    },
    onClickRight() {
      this.$router.push({
        name: "propertyOrderList"
      });
    },
    addNewRoom() {
      this.$router.push("./selectArea");
    },
    /* 跳转到常用电话 */
    gotoCommonPhone(){
      this.$router.push({ name: "commonPhone" }); //常用电话
    }
  }
};
</script>

<style scoped>
.propertyBillList {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.propertyBillList .rightBtn{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 32px;
    padding-right: 10px;
    width: 100%;
    text-align: right;
}
.propertyBillList .rightBtnThemeL {
  font-size: 17px;
}
.propertyBillListContent {
  padding-bottom: 100px;
  flex: 1;
  overflow-y: scroll;
}
.billAlert {
  font-size: 14px;
  background: #fff2dd;
  box-shadow: inset 0 6px 7px 0 #f9e4c2;
  padding: 10px 16px;
  text-align: center;
}
.billAlertThemeL {
  font-size: 15px;
}
.propertyBillList .order_itme_list {
  margin-top: 15px;
  border-radius: 6px;
  box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
  margin-left: 5%;
  margin-right: 5%;
}

.propertyBillList .orderHeader {
  background: #fff;
  border: 1px solid #efefef;
  height: 65px;
  /*margin: 0 20px;*/
  padding-top: 15px;
  display: flex;
  border-radius: 6px 6px 0 0;
}

.propertyBillList .user_title {
  font-family: PingFangSC-Medium;
  font-size: 16px;
  color: #3a3a3a;
  letter-spacing: 0.06px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 270px;
}
.propertyBillList .user_title_themeL {
  font-size: 18px;
}
.propertyBillList .user_name {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: 0.05px;
}
.propertyBillList .user_name_themeL {
  font-size: 15px;
  height: 25px;
  line-height: 25px;
}
.propertyBillList .freeText {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #3a3a3a;
  letter-spacing: 0.05px;
  height: 45px;
  line-height: 45px;
}
.propertyBillList .freeTextThemeL {
  font-size: 15px;
  padding-left: 8px;
  padding-right: 8px;
}

.propertyBillList .freeShow {
  display: flex;
  justify-content: space-between;
  height: 45px;
  background: #f6f6f8;
  padding: 0 10px;
  border: 1px solid #ececec;
  border-top: none;
}

.propertyBillList .bottomPanel {
  position: fixed;
  bottom: 0px;
  left: 0px;
  width: 100%;
  background-color: #fff;
  z-index: 100;
}
.propertyBillList .bottomPanel .addRoom {
  font-size: 16px;
  color: #fb5768;
  background: #ffffff;
  box-shadow: 0 0 8px 0 rgba(189, 189, 189, 0.29);
  border-radius: 6px;
  height: 55px;
  line-height: 55px;
  text-align: center;
  margin: 16px;
}
.propertyBillList .bottomPanel .addRoomThemeL {
  font-size: 18px;
}
.propertyBillList .monthUnderRadius {
  border-radius: 0 0 6px 6px;
}
.propertyBillList .goCommonPhone{
  width: 100%;
  text-align: center;
  font-size: 14px;
  height: 30px;
  line-height: 30px;
  margin-top: 20px;
  color: #fb5768;
  text-decoration: underline;
}
.propertyBillList .goCommonPhoneThemeL{
  font-size: 15px;
}
</style>
